.widget {
   background-color: white;
   box-shadow: 1px 3px 8px 0px $iconGray, -1px 3px 8px 0px $iconGray;
   padding: 15px;
   border-radius: 10px;
   &__title {
      font-size: 16px;
      margin-bottom: 20px
   }
   &__content {
      @include f(start, start);
      margin-bottom: 20px
   }
   &__text-box {
      @include f(start, start);
      flex-direction: column;
      margin-right: 20px;
   }
   &__text {
      &_gray {
         color: $iconGray;
         margin-bottom: 10px;
      }
   }
   &__item {
      @include f(start, center);
      gap: 10px;
      margin-bottom: 20px;
      &_address {
         position: relative;
      }
      &_big{
         @include f(start, start);
         margin-bottom: 0px;
      }
      &_border{
         padding-bottom: 10px;
         border-bottom: 1px solid rgb(242, 242, 244);
      }
      &-btn{
         height: 34px;
         min-width: 34px;
         border-radius: 10px;
         background-color: rgb(245, 245, 245);
         margin-right: 3px;
         @include f(center, center);
         transition: border 0.2s;
         &_selected{
            border: 2px solid black;
        }
         &_vk{
            background-color: rgb(70, 128, 194);
            @include f(center, center);
         }
         &_telegram{
            background-color: rgb(68, 166, 255);
            @include f(center, center);
         }
         &_viber{
            background-color: rgb(116, 96, 242);
            @include f(center, center);
         }
         &_ok{
            background-color: rgb(238, 130, 8);
            @include f(center, center);
         }
         &_mailru{
            background-color: rgb(1, 94, 249);
            @include f(center, center);
         }
         &_big{
            height: 40px;
            width: 100%;
            border-radius: 10px;
            background-color: rgb(245, 245, 245);   
            transition: background-color 0.3s, transform 0.1s;
            &:hover{
                background-color: $orange;
                color: white;
            }
         }
      }
   }
   &__item-text {
        width: auto;
        font-weight: 500;
        word-wrap: break-word;
        &_green {
            user-select: none;
            color: green;
        }
        &_red{
            color: rgb(244, 67, 54);
        }
        &_orange{
            color: rgb(255, 171, 0);
        }
        &_big{
           @include f(start, start);
           flex-direction: column;
        }
        &_linkOnMap{
           cursor: pointer;
           color: blue;
           position: absolute;
           bottom: -3px;
           right: 0;
        }
    }
   &__item-btn {
   }
}

