.categories {
  margin-bottom: 50px;
  &__title {
     font-size: 28px;
     margin-bottom: 20px;
     font-weight: 900;
  }
  &__img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
   }
  &__items {
     display: grid;
     grid-template-columns: repeat(6, 1fr);
     grid-template-rows: repeat(2, 1fr);
     gap: 20px;
  }
&__item {
   cursor: pointer;
   border-radius: 10px;
   padding: 30px;
   position: relative;
   transition: background-color 0.3s ease;
   width: 153px;
   height: 103px;
   &::before{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      z-index: 2;
      background-color: rgba(153, 153, 153, 0.9);
      transition: background-color 0.3s ease;
   }
   &:hover::before{
      background-color: rgba(153, 153, 153, 0.5);
   }
   &_active::before{
         background-color: transparent;
   }
   &_active:hover::before{
      background-color: transparent;
   }
   }
    &__text {
     user-select: none;
     text-align: center;
     color: white;
     font-size: 16px;
     position: absolute;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
     z-index: 3;
  }
}

